body {
  background: navy;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

#weatherApp {
  backdrop-filter: blur(20px);
  text-align: center;
  background: #ffffff1a;
  border: 1px solid #ffffff1a;
  border-radius: 15px;
  max-width: 800px;
  padding: 20px;
  box-shadow: 0 8px 32px #0000001a;
}

h2, label, p {
  color: #fff;
  margin: 8px 0;
}

input {
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 10px;
  width: calc(100% - 16px);
  margin-top: 20px;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background: #528aae;
  border: none;
  border-radius: 10px;
  width: 150px;
  margin-top: 20px;
  padding: 10px;
  font-size: 15px;
}

button:hover {
  background: #152238;
}

#main-div p {
  margin-top: -30px;
  font-size: 60px;
}

#liveWeather-info {
  font-size: 20px;
}

#weather-icon {
  width: 200px;
  height: 200px;
  margin: 0 auto;
  display: none;
}

#daily-forecast {
  white-space: nowrap;
  justify-content: space-between;
  margin-top: 50px;
  display: flex;
  overflow-x: auto;
}

.daily-item {
  color: #fff;
  flex-direction: column;
  flex: none;
  align-items: center;
  width: 80px;
  margin-right: 10px;
  display: flex;
}

.daily-item img {
  width: 30px;
  height: 30px;
  margin-bottom: 5px;
}

#daily-heading {
  color: #fff;
  margin-top: 10px;
}

#forcast {
  display: none;
}
